import React, { Fragment, useEffect, useState } from "react";
import MenuIcon from "@mui/icons-material/Menu";
import ArrowDropDownIcon from "@mui/icons-material/ArrowDropDown";
import Map from "../components/Map";
import Menu from "../components/Menu";
import "./Home.css";
import { EventItem } from "../components/EventItem";
import axios from "axios";
import { Dialog, DialogContent, MenuItem, Select } from "@mui/material";
import ClearIcon from "@mui/icons-material/Clear";
import {
  MapContainer,
  Marker,
  Popup,
  LayersControl,
  LayerGroup,
  TileLayer,
} from "react-leaflet";
import * as L from "leaflet";
// import { useAppContext } from "../context/appContext";

const Home = () => {
  const [data, setData] = useState([]); //API
  const [searchInput, setSearchInput] = useState("");
  const [showDialogue, setShowDialogue] = useState(false);
  const [prefecture, setPrefecture] = useState("");
  const [genre, setGenre] = useState("");
  const [date, setDate] = useState("");
  const [filteredList, setFilteredList] = useState([]);

  const LeafIcon = L.Icon.extend({
    options: {},
  });

  const greenIcon = new LeafIcon({
    iconUrl:
      "https://chart.apis.google.com/chart?chst=d_map_pin_letter&chld=%E2%80%A2|2ecc71&chf=a,s,ee00FFFF",
  });

  const redIcon = new LeafIcon({
    iconUrl:
      "https://maps.gstatic.com/mapfiles/api-3/images/spotlight-poi2_hdpi.png",
    iconSize: [20, 36],
  });

  const fetchData = async () => {
    try {
      const { data } = await axios.get(
        "http://localhost:8000/api/spot_search/"
      );
      setData([data]);
      setFilteredList([data]);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    fetchData();
  }, []);

  const inputHandler = (e) => {
    setSearchInput(e.target.value);
  };

  const searchSubmitHandler = (e) => {
    e.preventDefault();

    if (searchInput.trim() !== "") {
      const searchFilteredData = filteredList
        .flat()
        .filter((item) => item.name.includes(searchInput));
      setData(searchFilteredData);
      setSearchInput("");
      // setIsClear(false);
    } else {
      return;
    }
  };

  const submitHandler = (e) => {
    e.preventDefault();
    if (!date || !genre || !prefecture) {
      return;
    } else {
      const deeplyFilteredData = filteredList.flat().filter((item) => {
        return (
          (item.genre === genre &&
            item.start_date.substring(5, 7).includes(date.charAt(0))) ||
          item.address.includes(prefecture)
        );
      });
      setData(deeplyFilteredData);
      setGenre("");
      setPrefecture("");
      setDate("");
      setShowDialogue(false);
    }
  };

  return (
    <Fragment>
      <header>
        <div className="title">
          <h2></h2>
          <Menu />
        </div>
      </header>

      <div className="map-container">
        <div className="event-search-container">
          <input
            className="event-search"
            type="text"
            name="event"
            placeholder=""
            onChange={inputHandler}
          />
          <button
            type="submit"
            className="btn-search"
            onClick={searchSubmitHandler}
          >
            
          </button>
          <ArrowDropDownIcon
            className="filter-icon"
            onClick={() => setShowDialogue(!showDialogue)}
          />
        </div>
        <div className="map-box">
          <Map data={data} filteredList={filteredList} />
        </div>
      </div>

      <div className="color-container">
        <div className="green"></div>
        <div className="red"></div>
        <div className="blue"></div>
      </div>

      <div className="eventList-container">
        <div className="event-list-title">
          <h2></h2>
        </div>
        {data.flat().map((item) => {
          const { name, address, start_date, id } = item;
          return (
            <EventItem
              name={name}
              address={address}
              date={start_date}
              key={id}
              spotId={id}
              data={data}
            />
          );
        })}
      </div>

      <Dialog
        open={showDialogue}
        fullWidth
        maxWidth="md"
        PaperProps={{
          style: {
            boxShadow: "none",
            textAlign: "center",
            width: "auto",
          },
        }}
      >
        <DialogContent>
          <form onSubmit={submitHandler}>
            <div className="filter-container">
              <div className="date-box">()</div>
              <Select
                onChange={(e) => setDate(e.target.value)}
                className="selector"
              >
                <MenuItem value="1">1</MenuItem>
                <MenuItem value="2">2</MenuItem>
                <MenuItem value="3">3</MenuItem>
                <MenuItem value="4">4</MenuItem>
                <MenuItem value="5">5</MenuItem>
                <MenuItem value="6">6</MenuItem>
                <MenuItem value="7">7</MenuItem>
                <MenuItem value="8">8</MenuItem>
                <MenuItem value="9">9</MenuItem>
                <MenuItem value="10">10</MenuItem>
                <MenuItem value="11">11</MenuItem>
                <MenuItem value="12">12</MenuItem>
              </Select>
              <ClearIcon
                className="clearIcon"
                onClick={() => setShowDialogue(false)}
              />
            </div>

            <div className="filter-container">
              <div className="date-box"></div>
              <Select
                onChange={(e) => setGenre(e.target.value)}
                className="selector"
              >
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
              </Select>
            </div>

            <div className="filter-container">
              <div className="date-box"></div>
              <Select
                onChange={(e) => setPrefecture(e.target.value)}
                className="selector"
              >
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
              </Select>
            </div>

            <button className="btn"></button>
          </form>
        </DialogContent>
      </Dialog>
    </Fragment>
  );
};

export default Home;
